<style lang="less">
@import "../assets/less/list.less";
</style>
<style lang="less" scoped>
.node-content {
  margin: 20px;
  width: 100%;
  min-height: 100px;
  line-height: 26px;
  text-indent: 2em;
}
.guestbook {
  width: 100%;
  height: 283px;
  // background: url("../assets/img/site/contact.jpg") no-repeat center center;
  .guestbook-c {
    width: 1200px;
    height: 100%;
    margin: 0 auto;
    padding-top: 1px;
    .gstitle {
      color: #fff;
      margin: 69px 0 0 0;
      p {
        margin: 20px 0;
      }
    }
    .p4 {
      font-size: 12px;
      color: #ccc;
    }
  }
}
</style>

<template>
  <div class="router-main">
    <base-bannerimg
      :img-url="parentNode.timg"
      :nodename="parentNode.name"
      :remark="parentNode.remark"
    ></base-bannerimg>
    <div id="main" style="display:block;">
      <h1 style="text-align:center;">{{tree.name}}</h1>
      <div class="node-content" v-html="tree.brief"></div>
    </div>
    <div class="guestbook">
      <div class="guestbook-c">
        <div class="gstitle">
          <h2>我猜您现在可能存在如下疑问：</h2>
          <p>你们能做什么？需要多少钱？需要多久？我需要做什么?需要提供什么资料？售后服务怎么样？</p>
        </div>
        <base-guestbook></base-guestbook>
        <p class="p4">同样的问题每个人侧重点不同，很难有标准的答案；请认真填写表单信息或直接在线联系，我们会给您最合适的答案！</p>
      </div>
    </div>
  </div>
</template>

<script>
import guestbook from "../components/base-guestbook/index";
export default {
  data() {
    return {
      tree: {},
      crumbs: [],
      data: [],
      param: {},
      parentNode: {}
    };
  },
  methods: {
    async getNodeData(nodeid) {
      const doc = await this.$tools.httpAgent(
        "/api/tree/get",
        "post",
        "id=" + nodeid
      );
      this.tree = doc.data;
      this.$tools.setSeo(doc.data.name, doc.data.keyword, doc.data.seodes);
    },
    getSister: async function (nodeid) {
      const sister = await this.$tools.httpAgent("/api/tree/sister", "post", {
        id: nodeid
      });
      this.data = sister.data.nodelist;
    },
    getParent: async function (nodeid) {
      const doc = await this.$tools.httpAgent("/api/tree/parent", "post", {
        id: nodeid
      });
      this.parentNode = doc.data;
      console.log(this.parentNode);
    }
  },
  components: {
    "base-guestbook": guestbook
  },
  async mounted() {
    const param = this.$route.params;
    const nodeid = param.nodeid;
    this.getParent(nodeid);
    this.getNodeData(nodeid);
  },
  watch: {
    $route: async function () {
      const param = this.$route.params;
      const nodeid = param.nodeid;
      // this.getSister(nodeid);
      this.getNodeData(nodeid);
    }
  }
};
</script>
